body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.board {
  width: 400px;
  margin: 25px auto;
}

.left, .right {
  flex-grow: 1;
}

.rank {
  display: flex;
  flex-direction: row;
}

.rank:last-child {
  border-bottom: 1px solid black;
}

.space {
  width: 50px;
  height: 50px;
  border: 1px solid black;
  border-bottom: 0px;
  border-right: 0px;
}

.space:last-child {
  border-right: 1px solid black;
}

.space.white {
  background: white;
}

.space.black {
  background: gray;
}

.space.highlighted {
  background: green;
}

.instruction {
  text-align: center;
  font-size: 4rem;
  font-weight: 900;
  text-transform: uppercase;
  color: #444;
}

.results {
  margin-top: 100px;
  width: 100%;
}

.result {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

